<template>
    <div class="container">
        <h1>Latest Post</h1>
        <div class="create-post">
            <label for="create-post">Say something...</label>
            <input type="text" id="create-post" v-model="text" placeholder="Create a post" />
            <button v-on:click="createPost()">Post!</button>
        </div>
        <hr />
        <p class="error" v-if="error">{{ error }}</p>
        <div class="post-container">
            <div
                class="post"
                v-bind:item="post"
                v-bind:index="index"
                v-bind:key="post.id"
                v-for="(post, index) in posts"
                v-on:dblclick="deletePost(post.id)"
            >
                <!-- {{ `${post.createAt.getDate()}/${post.createAt.getMonth()}/${post.createAt.getFullYear()}` }} -->
                <!-- <p class="text">Date: {{ post.Date }}</p>
                <p class="text">TimeSleep: {{ post.TimeSleep }}hr</p>
                <p class="text">WalkStep: {{ post.WalkStep }} steps</p> -->
                <p>{{post.text}}</p>
            </div>
        </div>
    </div>
</template>

<script>
import PostService from "../PostService";

export default {
    name: "PostComponent",
    data() {
        return {
            posts: [], // to be filled with the data from the backend
            error: "",
            text: "",
        };
    },
    async created() {
        try {
            this.posts = await PostService.getPosts();
        } catch (err) {
            this.error = err.message;
        }
    },

    methods: {
        async createPost() {
            await PostService.insertPost(this.text);
            this.posts = await PostService.getPosts();
        },
        async deletePost(id) {
            await PostService.deletePost(id);
            this.posts = await PostService.getPosts();
        },
    },
};
</script>

